<template>
  <div class="container">
    <div class="form">
      <el-form label-width="120px" :model="queryData" :rules="rules" :inline="true" ref="queryForm">
        <el-form-item label="检视类型" prop="type">
          <el-select v-model="queryData.type" placeholder="请选择检视类型" @change="typeChange">
            <el-option
            v-for="item in typeList"
            :key="item.code"
            :value="item.code"
            :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属机构" prop="org">
          <el-select v-model="queryData.org" placeholder="请选择所属机构">
            <el-option
              v-for="item in orgList"
              :key="item.code"
              :value="item.code"
              :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属渠道" prop="channel">
          <el-select v-model="queryData.channel" placeholder="请选择所属渠道">
            <el-option
              v-for="item in channelList"
              :key="item.code"
              :value="item.code"
              :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="预算版本号" prop="version">
          <el-input v-model="queryData.version"></el-input>
        </el-form-item>
        <el-form-item label="起始日期" prop="startDate">
          <el-date-picker
            type="date"
            placeholder="请选择起始日期"
            v-model="queryData.startDate"
            style="width: 100%;">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="截止日期" prop="endDate">
          <el-date-picker
            type="date"
            placeholder="请选择截止日期"
            v-model="queryData.endDate"
            style="width: 100%;">
          </el-date-picker>
        </el-form-item>
      </el-form>
    </div>
    <div class="option">
      <el-button plain type="primary" size="small" @click="resetForm">重置</el-button>
      <el-button plain type="primary" size="small" @click="searchData">查询</el-button>
      <el-button plain type="primary" size="small" @click="exportData">导出</el-button>
    </div>
    <div class="data" :hidden.sync="dataHidden">
      <div class="table">
        <el-table style="width: 100%"  :data="curData" stripe :header-cell-style="tableHeader">
          <el-table-column
            header-align="center"
            align="center"
            v-for="column in columns"
            :key="column.prop"
            :prop="column.prop"
            :label="column.label"
            :min-width="column.width"
            v-if="column.isShow">
          </el-table-column>
        </el-table>
        <base-pagination
          :total="data.length"
          @dataChange="dataChange">
        </base-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const BasePagination = () => import('@/components/BasePagination')
export default {
  name: 'Budget',
  components: {BasePagination},
  data () {
    return {
      dataHidden: true,
      queryData: {
        type: '',
        org: '50000',
        channel: '001',
        version: '50000201801010900',
        startDate: '2018-01-01',
        endDate: '2018-03-01'
      },
      rules: {
        type: [{required: true, trigger: 'change', message: '检视类型不能为空'}],
        org: [{required: true, trigger: 'change', message: '所属机构不能为空'}],
        channel: [{required: true, trigger: 'change', message: '所属渠道不能为空'}],
        startDate: [{required: true, trigger: 'blur', message: '起始日期不能为空'}],
        endDate: [{required: true, trigger: 'blur', message: '截止日期不能为空'}]
      },
      typeList: [
        {code: 'fee', name: '预算对比-配费模式'},
        {code: 'org', name: '预算对比-分机构'},
        {code: 'biz', name: '预算对比-分业务板块'}
      ],
      orgList: [
        {code: '10000', name: '北京分公司'},
        {code: '20000', name: '上海分公司'},
        {code: '30000', name: '江苏省分公司'},
        {code: '40000', name: '山东省分公司'},
        {code: '50000', name: '山西省分公司'}
      ],
      channelList: [
        {code: '001', name: '传统渠道'},
        {code: '002', name: '网电渠道'}
      ],
      columns: [
        {prop: 'version', label: '预算版本号', width: 120, isShow: true},
        {prop: 'feeModel', label: '配费模式', width: 120, isShow: false},
        {prop: 'bizSector', label: '业务板块', width: 120, isShow: false},
        {prop: 'inspectIndex', label: '检视指标', width: 120, isShow: true},
        {prop: 'inspectResult', label: '检视结果', width: 120, isShow: true},
        {prop: 'inspectDate', label: '检视日期', width: 120, isShow: true}
      ],
      currentPage: 1,
      pageSize: 5,
      data: [],
      feeData: [
        {
          version: '50000201801010900',
          feeModel: '个车分类',
          bizSector: '',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算200万',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '车队协议',
          bizSector: '',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算5%',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '代理协议',
          bizSector: '',
          inspectIndex: '经营成本率-对比差值',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '个车分类',
          bizSector: '',
          inspectIndex: '市场费用率-实际值',
          inspectResult: '超过预算10%',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '车队协议',
          bizSector: '',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算100万',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '代理协议',
          bizSector: '',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        }
      ],
      orgData: [
        {
          version: '50000201801010900',
          feeModel: '',
          bizSector: '',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算200万',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '',
          bizSector: '',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算5%',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '',
          bizSector: '',
          inspectIndex: '经营成本率-对比差值',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '',
          bizSector: '',
          inspectIndex: '市场费用率-实际值',
          inspectResult: '超过预算10%',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '',
          bizSector: '',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算100万',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '',
          bizSector: '',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        }
      ],
      bizData: [
        {
          version: '50000201801010900',
          feeModel: '个车分类',
          bizSector: '家庭自用客车',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算200万',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '车队协议',
          bizSector: '非营业企业客车',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算5%',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '代理协议',
          bizSector: '营业客车',
          inspectIndex: '经营成本率-对比差值',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '个车分类',
          bizSector: '摩托车和拖拉机',
          inspectIndex: '市场费用率-实际值',
          inspectResult: '超过预算10%',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '车队协议',
          bizSector: '非营业货车',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算100万',
          inspectDate: '2018-01-21'
        },
        {
          version: '50000201801010900',
          feeModel: '代理协议',
          bizSector: '营业货车',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        }
      ]
    }
  },
  methods: {
    typeChange (val) {
      this.currentPage = 1
      if (val === 'fee') {
        this.columns.forEach(item => {
          if (item.prop === 'feeModel') {
            item.isShow = true
          } else if (item.prop === 'bizSector') {
            item.isShow = false
          }
        })
        this.data = this.feeData
        this.dataHidden = false
      } else if (val === 'org') {
        this.columns.forEach(item => {
          if (item.prop === 'feeModel') {
            item.isShow = false
          } else if (item.prop === 'bizSector') {
            item.isShow = false
          }
        })
        this.data = this.orgData
        this.dataHidden = false
      } else if (val === 'biz') {
        this.columns.forEach(item => {
          if (item.prop === 'feeModel') {
            item.isShow = true
          } else if (item.prop === 'bizSector') {
            item.isShow = true
          }
        })
        this.data = this.bizData
        this.dataHidden = false
      } else {
        this.columns.forEach(item => {
          if (item.prop === 'feeModel') {
            item.isShow = false
          } else if (item.prop === 'bizSector') {
            item.isShow = false
          }
        })
        this.data = []
        this.dataHidden = true
      }
    },
    resetForm () {
    },
    searchData () {
      this.$refs['queryForm'].validate(valid => {
        if (valid) {
          this.currentPage = 1
          if (this.queryData.type === 'fee') {
            this.columns.forEach(item => {
              if (item.prop === 'feeModel') {
                item.isShow = true
              } else if (item.prop === 'bizSector') {
                item.isShow = false
              }
            })
            this.data = this.feeData
            this.dataHidden = false
          } else if (this.queryData.type === 'org') {
            this.columns.forEach(item => {
              if (item.prop === 'feeModel') {
                item.isShow = false
              } else if (item.prop === 'bizSector') {
                item.isShow = false
              }
            })
            this.data = this.orgData
            this.dataHidden = false
          } else if (this.queryData.type === 'biz') {
            this.columns.forEach(item => {
              if (item.prop === 'feeModel') {
                item.isShow = true
              } else if (item.prop === 'bizSector') {
                item.isShow = true
              }
            })
            this.data = this.bizData
            this.dataHidden = false
          } else {
            this.columns.forEach(item => {
              if (item.prop === 'feeModel') {
                item.isShow = false
              } else if (item.prop === 'bizSector') {
                item.isShow = false
              }
            })
            this.data = []
            this.dataHidden = true
          }
        }
      })
    },
    exportData () {
    },
    dataChange (size, page) {
      this.pageSize = size
      this.currentPage = page
    }
  },
  computed: {
    curData () {
      const result = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end > this.data.length ? this.data.length : end
      for (let i = start; i < end; ++i) {
        result.push(this.data[i])
      }
      return result
    },
    ...mapState({tableHeader: state => state.app.tableHeader})
  }
}
</script>

<style scoped>

</style>



